import React from "react";
import { Table } from "@faharmony/table";
import { faTerminal } from "@fortawesome/pro-light-svg-icons";

interface IDataProps {
  name: string;
  age: number;
  // subRows?: IDataProps[];
}

export default () => {
  const tableData: IDataProps[] = React.useMemo(
    () => [
      { name: "Siddhant Gupta", age: 25 },
      { name: "Other person", age: 25 },
    ],
    []
  );

  const tableColumns: any[] = React.useMemo(
    () => [
      { accessor: "name", Header: "Full name", disableFilter: false },
      { accessor: "age", Header: "Current age", disableFilter: true },
    ],
    []
  );

  const handleRowClick = (rowObject: IDataProps) => console.log(rowObject);

  const singleRowActions = (rowObject: IDataProps) => [
    {
      id: "log",
      icon: faTerminal,
      tooltip: "Log",
      onClick: () => console.log(rowObject),
    },
  ];

  return (
    <Table<IDataProps>
      tableHeading="Table"
      data={tableData}
      columns={tableColumns}
      handleRowClick={handleRowClick}
      singleRowActions={singleRowActions}
      enableSorting
      enableFiltering
      enablePagination
      enableMultipleRowSelect
      enableColumnVisibilitySelector
      exportTable
    />
  );
};

